  text-align: right;
}

blockquote p:last-of-type {
  margin-bottom: 0;
}

/* iframe */
iframe {
  border: 0;
}

/* Forms ========================================================================== */
button, input:not([type="radio"]):not([type="checkbox"]),
select {
